import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";

export default class AddTutorial extends Component {
  constructor(props) {
    super(props);
    this.onChangeCategory = this.onChangeCategory.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.onChangeId = this.onChangeId.bind(this);
    this.onChangePrice = this.onChangePrice.bind(this);
    this.onChangeSection = this.onChangeSection.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeDownload = this.onChangeDownload.bind(this);
    this.onChangeDemo = this.onChangeDemo.bind(this);
    this.onChangeUrl1 = this.onChangeUrl1.bind(this);
    this.onChangeUrl2 = this.onChangeUrl2.bind(this);
    this.onChangeUrl3 = this.onChangeUrl3.bind(this);
    this.saveTutorial = this.saveTutorial.bind(this);
    this.newTutorial = this.newTutorial.bind(this);

    this.state = {
      category: "",
      description: "",
      id: "",
      price: "",
      section: "",
      title: "",
      download: "",
      demo:"",
      url1:"",
      url2:"",
      url3:"",
      urls: []
    };
  }

  onChangeCategory(e) {
    this.setState({
      category: e.target.value,
    });
  }

  onChangeDescription(e) {
    this.setState({
      description: e.target.value,
    });
  }
  onChangeId(e) {
    this.setState({
      id: e.target.value,
    });
  }
  onChangePrice(e) {
    this.setState({
      price: e.target.value,
    });
  }
  onChangeSection(e) {
    this.setState({
    section: e.target.value,
    });
  }
  onChangeTitle(e) {
    this.setState({
      title: e.target.value,
    });
  }
  onChangeDownload(e) {
    this.setState({
      download: e.target.value,
    });
  }
  onChangeDemo(e) {
    this.setState({
      demo: e.target.value,
    });
  }
  onChangeUrl1(e) {
    this.setState({
      url1: e.target.value,
    });
  }
  onChangeUrl2(e) {
    this.setState({
      url2: e.target.value,
    });
  }
  onChangeUrl3(e) {
    this.setState({
      url3: e.target.value,
    });
  }


  saveTutorial() {
    let data = {
      category: this.state.category,
      description: this.state.description,
      id: this.state.id,
      price: this.state.price,
      section: this.state.section,
      title: this.state.title,
      download: this.state.download,
      demo: this.state.demo,
      urls: [this.state.url1,this.state.url2,this.state.url3],
    };

    TutorialDataService.create( data)
      .then(() => {
        console.log("Created new item successfully!");
        this.setState({
          submitted: true,
        });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  newTutorial() {
    this.setState({
      category: "",
      description: "",
      id: "",
      price: "",
      section: "",
      title: "",
      download:"",
      demo:"",
      url1:"",
      url2:"",
      url3:"",
      urls: []
    });
  }

  render() {
    return (
      <div className="submit-form">
        {this.state.submitted ? (
          <div>
            <h4>You submitted successfully!</h4>
            <button className="btn btn-success" onClick={this.newTutorial}>
              Add
            </button>
          </div>
        ) : (
          <div>
            
         <div style={{display:"flex"}}>
            <div className="form-group">
              <label htmlFor="section">section</label>
              <select
              className="form-control"
              id="section"
              required
              value={this.state.section}
              onChange={this.onChangeSection}
              name="section"
              > 
              <option ></option>
              
              <option value="Featured">GAMES</option>
              <option value="TopSelling">TEMPLATES</option>
              <option value="Fonts">FONTS</option>
           

              </select>
            </div>
            <div className="form-group">
              <label htmlFor="category">category</label>
              <select
                className="form-control"
                id="category"
                required
                value={this.state.category}
                onChange={this.onChangeCategory}
                name="category"
              > 
              <option ></option>
              <option value="Admin">Admin Templates</option>
              <option value="Email">Email Templates</option>
              <option value="Landing">Landing Page Templates</option>
              <option value="Site">Site Templates</option>
              <option value="WordPress">WordPress</option>
              <option value="Fonts">Fonts</option>

              </select>
            </div>
         
           
            </div>
            <div style={{display:"flex"}} >
            <div className="form-group">
              <label htmlFor="title">title</label>
              <input
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.title}
                onChange={this.onChangeTitle}
                name="title"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">download</label>
              <input
                type="text"
                className="form-control"
                id="demo"
                required
                value={this.state.download}
                onChange={this.onChangeDownload}
                name="demo"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">demo</label>
              <input
                type="text"
                className="form-control"
                id="demo"
                required
                value={this.state.demo}
                onChange={this.onChangeDemo}
                name="demo"
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="description">Picture2</label>
              <input
                type="text"
                className="form-control"
                id="url2"
                required
                value={this.state.url2}
                onChange={this.onChangeUrl2}
                name="url2"
              />
            </div>
            <div className="form-group">
              <label htmlFor="description">Picture1</label>
              <input
                type="text"
                className="form-control"
                id="url1"
                required
                value={this.state.url1}
                onChange={this.onChangeUrl1}
                name="url1"
              />
            </div>
      
            <div className="form-group">
              <label htmlFor="description">Picture3</label>
              <input
                type="text"
                className="form-control"
                id="url3"
                required
                value={this.state.url3}
                onChange={this.onChangeUrl3}
                name="url3"
              />
            </div>
            </div>
            <div className="form-group">
              <label htmlFor="description">description</label>
              <textarea 
          
                className="form-control"
                id="description"
                required
                value={this.state.description}
                onChange={this.onChangeDescription}
                name="description"
              />
            </div>
            <button onClick={this.saveTutorial} className="btn btn-success">
              Submit
            </button>
          </div>
         
        )}
      </div>
    );
  }
}
