import React, { Component } from "react";
import TutorialDataService from "../services/tutorial.service";

export default class Tutorial extends Component {
  constructor(props) {
    super(props);
    this.onChangeCategory = this.onChangeCategory.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeDownload = this.onChangeDownload.bind(this);
    this.onChangeDemo = this.onChangeDemo.bind(this);
    this.onChangeSection = this.onChangeSection.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.onChangeUrl1=this.onChangeUrl1.bind(this);
    this.onChangeUrl2=this.onChangeUrl2.bind(this);
    this.onChangeUrl3=this.onChangeUrl3.bind(this);

    this.updatePublished = this.updatePublished.bind(this);
    this.updateTutorial = this.updateTutorial.bind(this);
    this.deleteTutorial = this.deleteTutorial.bind(this);

    this.state = {
      currentTutorial: {
        key: null,
        category: "",
        description: "",
        section: "",
        title: "",
        download:"",
        demo:"",
        urls:[],
        published: false,
      },
      message: "",
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const { tutorial } = nextProps;
    if (prevState.currentTutorial.key !== tutorial.key) {
      return {
        currentTutorial: tutorial,
        message: ""
      };
    }

    return prevState.currentTutorial;
  }

  componentDidMount() {
    this.setState({
      currentTutorial: this.props.tutorial,
    });
  }

  onChangeCategory(e) {
    const category = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          category: category,
        },
      };
    });
  }
  
  onChangeSection(e) {
    const section = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          section: section,
        },
      };
    });
  }

  onChangeUrl1(e) {
    const url1 = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          url1: url1,
        },
      };
    });
  }

  onChangeUrl2(e) {
    const url2 = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          url2: url2,
        },
      };
    });
  }
  onChangeUrl3(e) {
    const url3 = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          url3: url3,
        },
      };
    });
  }

  onChangeTitle(e) {
    const title = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          title: title,
        },
      };
    });
  }
  onChangeDownload(e) {
    const download = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          download: download,
        },
      };
    });
  }
  onChangeDemo(e) {
    const demo = e.target.value;

    this.setState(function (prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          demo: demo,
        },
      };
    });
  }

  onChangeDescription(e) {
    const description = e.target.value;

    this.setState((prevState) => ({
      currentTutorial: {
        ...prevState.currentTutorial,
        description: description,
      },
    }));
  }

  updatePublished(status) {
    TutorialDataService.update(this.state.currentTutorial.key, {
      published: status,
    })
      .then(() => {
        this.setState((prevState) => ({
          currentTutorial: {
            ...prevState.currentTutorial,
            published: status,
          },
          message: "The status was updated successfully!",
        }));
      })
      .catch((e) => {
        console.log(e);
      });
  }

  updateTutorial() {
    const data = {
      category: this.state.currentTutorial.category,
      section: this.state.currentTutorial.section,
      title: this.state.currentTutorial.title,
      download: this.state.currentTutorial.download,
      demo: this.state.currentTutorial.demo,
      description: this.state.currentTutorial.description,
      urls:[this.state.currentTutorial.url1,this.state.currentTutorial.url2,this.state.currentTutorial.url3],
    };

    TutorialDataService.update(this.state.currentTutorial.key, data)
      .then(() => {
        console.log(this.state.currentTutorial.key)
        this.setState({
          message: "The tutorial was updated successfully!",
        });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  deleteTutorial() {
    TutorialDataService.delete(this.state.currentTutorial.key)
      .then(() => {
        this.props.refreshList();
      })
      .catch((e) => {
        console.log(e);
      });
  }

  render() {
    const { currentTutorial } = this.state;

    return (
      <div>
        <h4>Tutorial</h4>
        {currentTutorial ? (
          <div className="edit-form">
            <form>
            <div className="form-group">
              <label htmlFor="section">section</label>
              <select
              className="form-control"
              id="section"
              required
              value={this.state.section}
              onChange={this.onChangeSection}
              name="section"
              > 
              <option ></option>
         
              <option value="Featured">GAMES</option>
              <option value="TopSelling">TEMPLATE</option>
              <option value="Fonts">FONTS</option>
           

              </select>
            </div>
            <div className="form-group">
              <label htmlFor="category">category</label>
              <select
                className="form-control"
                id="category"
                required
                value={this.state.category}
                onChange={this.onChangeCategory}
                name="category"
              > 
              <option ></option>
              <option value="Admin">Admin Templates</option>
              <option value="Email">Email Templates</option>
              <option value="Landing">Landing Page Templates</option>
              <option value="Site">Site Templates</option>
              <option value="WordPress">WordPress</option>
              <option value="Fonts">Fonts</option>

              </select>
            </div>

            <div className="form-group">
              <label htmlFor="description">description</label>
              <textarea 
          
                className="form-control"
                id="description"
                required
                value={this.state.description}
                onChange={this.onChangeDescription}
                name="description"
              />
            </div>
    
         
        
            <div className="form-group">
              <label htmlFor="title">title</label>
              <textarea
                type="text"
                className="form-control"
                id="title"
                required
                value={this.state.title}
                onChange={this.onChangeTitle}
                name="title"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">download</label>
              <input
                type="text"
                className="form-control"
                id="demo"
                required
                value={this.state.download}
                onChange={this.onChangeDownload}
                name="demo"
              />
            </div>
            <div className="form-group">
              <label htmlFor="title">demo</label>
              <input
                type="text"
                className="form-control"
                id="demo"
                required
                value={this.state.demo}
                onChange={this.onChangeDemo}
                name="demo"
              />
            </div>
       
            <div className="form-group">
              <label htmlFor="description">Picture1</label>
              <input
                type="text"
                className="form-control"
                id="url1"
                required
                value={this.state.url1}
                onChange={this.onChangeUrl1}
                name="url1"
              />
            </div>
            <div className="form-group">
              <label htmlFor="description">Picture2</label>
              <input
                type="text"
                className="form-control"
                id="url2"
                required
                value={this.state.url2}
                onChange={this.onChangeUrl2}
                name="url2"
              />
            </div>
            <div className="form-group">
              <label htmlFor="description">Picture3</label>
              <input
                type="text"
                className="form-control"
                id="url3"
                required
                value={this.state.url3}
                onChange={this.onChangeUrl3}
                name="url3"
              />
            </div>
           
            </form>

          

            <button
              className="badge badge-danger mr-2"
              onClick={this.deleteTutorial}
            >
              Delete
            </button>

            <button
              type="submit"
              className="badge badge-success"
              onClick={this.updateTutorial}
            >
              Update
            </button>
            <p>{this.state.message}</p>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Tutorial...</p>
          </div>
        )}
      </div>
    );
  }
}
